/* NPC Dialog bubble positioned beside the head icon */
.npc-dialog {
  position: fixed;
  top: clamp(18px, 3vh, 28px);     
  left: clamp(65px, 10vw, 100px);
  background: #2C3E50;
  color: #ECF0F1;
  border: 2px solid #34495E;
  border-radius: 12px;
  padding: 10px 14px;
  font-size: clamp(12px, 2.5vw, 14px);
  font-weight: 500;
  max-width: clamp(200px, 25vw, 280px);
  z-index: 1000;
  box-shadow: 
    0 8px 24px rgba(44, 62, 80, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.npc-dialog-content {
  position: relative;
  line-height: 1.4;
  font-family: var(--font-body);
}

/* Speech bubble tail pointing to head icon */
.npc-dialog-tail {
  position: absolute;
  top: 50%;
  left: -10px;
  transform: translateY(-50%);
  width: 0;
  height: 0;
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-right: 10px solid #2C3E50;
}

/* Responsive positioning */
@media (max-width: 768px) {
  .npc-dialog {
    top: clamp(15px, 2vh, 20px);
    left: clamp(48px, 12vw, 75px);
    max-width: clamp(180px, 40vw, 220px);
    padding: 8px 12px;
  }
  
  .npc-dialog-tail {
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent;
    border-right: 8px solid #2C3E50;
  }
}

/* Extra small screens */
@media (max-width: 480px) {
  .npc-dialog {
    top: clamp(12px, 1.5vh, 18px);
    left: clamp(40px, 15vw, 65px);
    max-width: clamp(160px, 45vw, 200px);
    font-size: clamp(11px, 2.2vw, 13px);
  }
}
