/* MacBook 13" and similar smaller laptops */
@media (max-width: 1440px) {
  .avatar-8bit-container {
    width: clamp(180px, 28vw, 300px);
    transform: translateY(-50%) translateX(-10px); /* Slight adjustment for smaller screens */
  }
  
  .avatar-8bit {
    height: clamp(200px, 38vh, 420px);
  }
  
  .work-bubble {
    left: clamp(-25px, -3.5vw, -15px);
    top: clamp(8px, 1.5vh, 28px);
    
    min-width: clamp(130px, 14vw, 155px);
    width: clamp(130px, 14vw, 155px);
    font-size: clamp(12px, 2.5vw, 15px);
  }
}

.avatar-8bit-container {
  position: absolute;       
  left: 0;                  
  top: 50%;                 
  transform: translateY(-50%); 
  width: clamp(200px, 35vw, 350px);  
  height: auto;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;         
  overflow: visible;
  z-index: 10;
}

.avatar-8bit {
  width: auto;
  height: clamp(220px, 42vh, 480px);  
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  filter: contrast(1.1) brightness(1.05);
}

.work-bubble {
  position: absolute;
  top: clamp(10px, 2vh, 30px);       
  left: clamp(-30px, -4vw, -10px);   
  background: #FFFFFF;
  border: 3px solid #6B3F2A;
  border-radius: 0;
  padding: 14px 18px;
  font-size: clamp(13px, 2.8vw, 16px);  
  font-weight: 600;
  color: #6B3F2A;
  white-space: nowrap;
  font-family: var(--font-body);
  z-index: 20;
  
  min-width: clamp(140px, 15vw, 165px);
  width: clamp(140px, 15vw, 165px);
  
  box-shadow: 
    0 0 0 1px rgba(107, 63, 42, 0.2),
    0 4px 8px rgba(107, 63, 42, 0.3),
    0 8px 16px rgba(107, 63, 42, 0.2),
    0 12px 24px rgba(0, 0, 0, 0.15),
    inset 0 1px 0 rgba(255, 255, 255, 0.8),
    inset 0 -1px 0 rgba(107, 63, 42, 0.1);
    
  animation: gameDialogPulse 3s ease-in-out infinite;
}

@keyframes gameDialogPulse {
  0%, 100% {
    box-shadow: 
      0 0 0 1px rgba(107, 63, 42, 0.2),
      0 4px 8px rgba(107, 63, 42, 0.3),
      0 8px 16px rgba(107, 63, 42, 0.2),
      0 12px 24px rgba(0, 0, 0, 0.15),
      inset 0 1px 0 rgba(255, 255, 255, 0.8),
      inset 0 -1px 0 rgba(107, 63, 42, 0.1);
  }
  50% {
    box-shadow: 
      0 0 0 2px rgba(107, 63, 42, 0.4),
      0 4px 8px rgba(107, 63, 42, 0.4),
      0 8px 16px rgba(107, 63, 42, 0.25),
      0 12px 24px rgba(0, 0, 0, 0.18),
      inset 0 1px 0 rgba(255, 255, 255, 0.9),
      inset 0 -1px 0 rgba(107, 63, 42, 0.15);
  }
}

.work-bubble.clickable {
  cursor: pointer;
}

.work-bubble.clickable:hover {
  transform: translateY(-2px);
  transition: transform 0.2s ease;
}

.dialog-indicator {
  position: absolute;
  bottom: 2px;
  right: 4px;
  font-size: 8px;
  color: rgba(107, 63, 42, 0.8);
  font-weight: normal;
  animation: gameArrowBounce 1.2s ease-in-out infinite;
}

@keyframes gameArrowBounce {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-3px);
  }
  100% {
    transform: translateY(0px);
  }
}

.bubble-tail,
.bubble-tail::after {
  display: none;
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .avatar-8bit-container {
    width: 45vw;               
    justify-content: center;   
    transform: translateY(-50%) translateX(0); /* Reset any desktop adjustments */
  }
  
  .avatar-8bit {
    height: clamp(180px, 38vh, 400px); 
  }
  
  .work-bubble {
    left: clamp(-25px, -3vw, -15px);
    top: clamp(5px, 1vh, 25px);
    padding: 12px 15px;
    
    min-width: clamp(120px, 18vw, 160px);
    width: clamp(120px, 18vw, 160px);
    
    box-shadow: 
      0 0 0 1px rgba(107, 63, 42, 0.3),
      0 3px 6px rgba(107, 63, 42, 0.3),
      0 6px 12px rgba(0, 0, 0, 0.1),
      inset 0 1px 0 rgba(255, 255, 255, 0.7);
  }

  .dialog-indicator {
    bottom: 4px;
    right: 8px;
    font-size: 7px;
    color: rgba(107, 63, 42, 0.7);
  }

  @keyframes gameArrowBounce {
    0% {
      transform: translateY(0px);
    }
    50% {
      transform: translateY(-2px);
    }
    100% {
      transform: translateY(0px);
    }
  }
}

.avatar-8bit-container:hover .work-bubble:not(.clickable) {
  border-color: #5D3426;
  background: #FEFEFE;
  transform: translateY(-1px);
  transition: all 0.3s ease;
}
