.projects-section {
  /* Use min-height to prevent vertical clipping */
  min-height: 70vh;
  padding: 40px 0 0;
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Allow content to flow from top */
  background-color: var(--color-bg, #f5f3ef);
  /* Removed overflow:hidden to allow full content visibility */
}

.projects-container {
  max-width: 1200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-sizing: border-box;
}

.projects-title {
  font-family: 'Thorilat', sans-serif;
  font-size: 2rem;
  color: var(--color-navy, #1a2a40);
  text-align: center;
  margin-bottom: 24px;
  letter-spacing: 2px;
}

.filter-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 30px;
  flex-wrap: wrap;
}

.filter-btn {
  font-family: 'Dansk', sans-serif;
  font-size: 0.85rem;
  font-weight: 500;
  padding: 10px 24px;
  min-width: 160px;
  background: #f5f3ef;
  color: var(--color-navy, #1a2a40);
  border: 3px solid var(--color-accent, #c2a85d);
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 
    0 3px 0 rgba(194, 168, 93, 0.4),
    0 6px 12px rgba(46, 46, 46, 0.15);
  letter-spacing: 0.5px;
}

.filter-btn:hover {
  transform: translateY(-2px);
  box-shadow: 
    0 5px 0 rgba(194, 168, 93, 0.5),
    0 8px 16px rgba(194, 168, 93, 0.25);
  background: var(--color-accent, #c2a85d);
  color: #f5f3ef;
}

.filter-btn.active {
  background: var(--color-navy, #1a2a40);
  border-color: var(--color-navy, #1a2a40);
  color: var(--color-accent, #c2a85d);
  box-shadow: 
    0 3px 0 rgba(26, 42, 64, 0.6),
    0 6px 12px rgba(26, 42, 64, 0.3);
}

.projects-scroll-wrapper {
  width: 100%;
  margin-bottom: 20px; /* This offset moves the scrollbar lower */
  position: relative;
}

.projects-grid {
  display: flex;
  gap: 25px;
  flex-wrap: nowrap;
  overflow-x: auto;
  overflow-y: hidden;
  min-width: 100%;
  padding: 30px 0 32px; /* Balanced padding for top and bottom */
  align-items: center;
  scrollbar-width: thin;
  scrollbar-color: rgba(194,168,93, 0.30) rgba(194, 168, 93, 0.20);
}

.projects-grid::-webkit-scrollbar {
  height: 12px;
  background: transparent;
}

.projects-grid::-webkit-scrollbar-track {
  background: rgba(194, 168, 93, 0.20);
  border-radius: 10px;
  margin: 0 20px;
}

.projects-grid::-webkit-scrollbar-thumb {
  background: rgba(194,168,93, 0.30);
  border-radius: 10px;
  border: 2px solid var(--color-bg, #f5f3ef);
  cursor: grab;
  min-width: 40px;
  transition: background 0.3s ease;
  background-clip: padding-box;
}

.projects-grid::-webkit-scrollbar-thumb:hover {
  background: rgba(194,168,93, 0.45);
}

.projects-grid::-webkit-scrollbar-thumb:active {
  background: rgba(194,168,93, 0.55);
  cursor: grabbing;
}

.project-card {
  background: linear-gradient(135deg, #faf8f5 0%, #f0ebe3 100%);
  border: 4px solid var(--color-accent, #c2a85d);
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 
    0 6px 0 rgba(194, 168, 93, 0.3),
    0 10px 25px rgba(46, 46, 46, 0.15);
  transition: all 0.3s ease;
  min-width: 280px;
  max-width: 280px;
  flex-shrink: 0;
  cursor: pointer;
}

.project-card:hover {
  transform: translateY(-6px);
  box-shadow: 
    0 10px 0 rgba(194, 168, 93, 0.4),
    0 14px 35px rgba(194, 168, 93, 0.25);
}

.project-image-container {
  position: relative;
  width: 100%;
  height: 140px;
  overflow: hidden;
  background: linear-gradient(135deg, #e8e3d9 0%, #d9d0c4 100%);
  border-bottom: 3px solid rgba(194, 168, 93, 0.3);
}

.project-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
  image-rendering: pixelated;
  opacity: 0.95;
}

.project-card:hover .project-image {
  transform: scale(1.05);
  opacity: 1;
}

.project-category-badge {
  position: absolute;
  top: 8px;
  right: 8px;
  font-family: 'Dansk', sans-serif;
  font-size: 0.65rem;
  font-weight: 600;
  padding: 4px 10px;
  background: rgba(26, 42, 64, 0.9);
  color: var(--color-accent, #c2a85d);
  border: 2px solid var(--color-accent, #c2a85d);
  text-transform: uppercase;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  letter-spacing: 0.8px;
  text-align: center;
}

.project-content {
  padding: 15px;
  text-align: center;
}

.project-title {
  font-family: 'Thorilat', sans-serif;
  font-size: 1.1rem;
  color: var(--color-navy, #1a2a40);
  text-shadow: 1px 1px 0 rgba(194, 168, 93, 0.3);
  letter-spacing: 1.5px;
  margin: 0;
}

.no-projects {
  text-align: center;
  padding: 40px 20px;
  font-family: 'Dansk', sans-serif;
  font-size: 1.1rem;
  color: var(--color-accent, #c2a85d);
  width: 100%;
}

/* Responsive Design */
@media (max-width: 768px) {
  .projects-section {
    min-height: 80vh;
    padding: 50px 0 0;
  }
  .projects-title {
    font-size: 1.7rem;
    margin-bottom: 15px;
  }
  .projects-grid {
    gap: 20px;
    padding: 24px 0 28px;
  }
  .projects-scroll-wrapper {
    margin-bottom: 15px;
  }
  .project-card {
    min-width: 240px;
    max-width: 240px;
  }
  .project-image-container {
    height: 120px;
  }
}

@media (max-width: 480px) {
  .projects-section {
    min-height: 90vh;
    padding: 24px 0 0;
  }
  .projects-title {
    font-size: 1.4rem;
    margin-bottom: 10px;
  }
  .projects-grid {
    gap: 15px;
    padding: 18px 0 20px;
  }
  .project-card {
    border-width: 3px;
    min-width: 200px;
    max-width: 200px;
  }
  .project-image-container {
    height: 100px;
  }
}
