.project-window-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  backdrop-filter: blur(8px);
  background-color: rgba(0,0,0,0.15);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1500;
  user-select: none;
}

.project-window {
  position: absolute;
  background: linear-gradient(135deg, #faf8f5 0%, #f0ebe3 100%);
  border-radius: 12px;
  border: 3px solid #c2a85d;
  box-shadow:
    0 0 35px rgba(194, 168, 93, 0.85),
    0 15px 35px rgba(46, 46, 46, 0.25);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  transition: box-shadow 0.25s ease;
}

.project-window:hover {
  box-shadow:
    0 0 60px rgb(4, 4, 3),
    0 20px 50px rgba(46, 46, 46, 0.35);
}

.project-window-header {
  cursor: grab;
  background: rgba(26, 42, 64, 0.85);
  color: #c2a85d;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Thorilat', sans-serif;
  padding: 12px 20px;
  user-select: none;
  font-size: 1.3rem;
  flex-shrink: 0;
  letter-spacing: 2px;
}

.project-window-header:active {
  cursor: grabbing;
}

.project-window-title {
  margin: 0;
  user-select: text;
}

.close-btn {
  cursor: pointer;
  background: transparent;
  border: none;
  font-size: 1.5rem;
  color: #c2a85d;
  font-weight: 700;
  transition: color 0.2s ease;
}

.close-btn:hover,
.close-btn:focus {
  color: #ffe55c;
  outline: none;
}

.project-window-content {
  padding: 20px;
  padding-bottom: 56px; /* space for absolutely fixed tech stack bar */
  overflow-y: auto;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.project-window-image {
  width: 100%;
  border-radius: 8px;
  object-fit: cover;
  height: auto;
  max-height: 240px;
  user-select: none;
  flex-shrink: 0;
}

.project-window-description {
  font-family: 'Courier New', monospace;
  font-size: 1rem;
  line-height: 1.4;
  color: #5a5a5a;
  overflow-wrap: break-word;
  max-height: 150px;
  overflow-y: auto;
  padding-right: 8px;
  padding-bottom: 20px; /* space for tech stack bar */
}

/* Show scrollbar only on hover */
.project-window-description::-webkit-scrollbar {
  width: 8px;
}
.project-window-description::-webkit-scrollbar-track {
  background: transparent;
}
.project-window-description::-webkit-scrollbar-thumb {
  background: rgba(194, 168, 93, 0.4);
  border-radius: 10px;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.project-window-description:hover::-webkit-scrollbar-thumb {
  opacity: 1;
}
.project-window-description::-webkit-scrollbar-thumb:hover {
  background: rgba(194, 168, 93, 0.8);
}

/* Tech stack fixed at very bottom */
.project-window-tech-stack {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;   /* Hug the container's bottom */
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  padding: 12px 20px 16px 20px;  /* space for resizer visual at right */
  background: linear-gradient(to top, rgba(250, 248, 245, 0.98), transparent);
  border-top: 2px solid rgba(194, 168, 93, 0.3);
  min-height: 44px;
  z-index: 10;
}

.project-tech-tag {
  background: #e8e3d9;
  color: #1a2a40;
  border-radius: 6px;
  padding: 5px 12px;
  font-family: 'Dansk', sans-serif;
  font-weight: 600;
  font-size: 0.85rem;
  user-select: none;
  border: 2px solid #c2a85d;
}

.project-window-resizer {
  position: absolute;
  bottom: 8px;
  right: 8px;
  width: 28px;
  height: 28px;
  cursor: se-resize;
  z-index: 20;
  background: url('data:image/svg+xml;utf8,<svg fill="%23c2a85d" height="24" viewBox="0 0 24 24" width="24" xmlns="http://www.w3.org/2000/svg"><path d="M0 0h24v24H0z" fill="none"/><path d="M14 14h1.5v1.5H14zm3 3h1.5v1.5H17zm-6 0h1.5v1.5H11zm3-6h1.5v1.5H14z"/></svg>') center center no-repeat;
  opacity: 0.7;
  transition: opacity 0.25s ease;
}

.project-window-resizer:hover {
  opacity: 1;
}

/* Responsive */
@media (max-width: 768px) {
  .project-window {
    width: 90vw !important;
    height: 70vh !important;
    max-width: none !important;
    max-height: none !important;
    bottom: auto !important;
    right: auto !important;
    left: 5vw !important;
    top: 10vh !important;
    border-radius: 10px;
  }

  .project-window-content {
    padding: 15px;
    padding-bottom: 66px;
  }
}

/* Portfolio Popup/NPC styles */

.portfolio-npc-dialog {
  background: #222531ee;
  color: #ffeaa7;
  border-radius: 8px;
  box-shadow: 0 4px 19px rgba(0,0,0,0.11);
  padding: 16px 28px;
  font-family: var(--font-body, monospace);
  font-size: 1.08rem;
  border: 2px solid #e8cb62;
  pointer-events: auto;
  max-width: 320px;
  text-align: center;
}

.portfolio-link {
  color: #907cfb;
  text-decoration: underline;
  cursor: pointer;
  transition: color 0.18s;
}

.portfolio-link:hover,
.portfolio-link:focus {
  color: #ffeaa7;
  outline: none;
}

/* Meta link row for centering the portfolio special link below the description */
.meta-link-row {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 12px 0 4px 0;  /* less vertical space */
  align-items: center;
  pointer-events: auto;
}

.portfolio-meta-link {
  display: inline-block;
  padding: 2px 9px;
  background: #fffbe5;
  color: #161428;
  border: 2px solid #ffe55c;
  border-radius: 13px;
  font-family: 'Fira Mono', monospace;
  font-weight: 700;
  letter-spacing: 0.7px;
  text-decoration: none;
  box-shadow: 0 0 4px #ffe55c44, 0 1px 4px #201a2222;
  position: relative;
  transition:
    box-shadow 0.19s,
    background 0.16s,
    color 0.14s,
    border 0.14s;
  cursor: pointer;
  outline: none;
  font-size: 0.78rem;
  text-align: center;
  min-width: 0;
}

.portfolio-meta-link:hover,
.portfolio-meta-link:focus {
  background: #fff277;
  color: #161428;
  border: 2.2px solid #ffe55c;
  box-shadow: 0 0 20px #ffe55c99, 0 4px 18px #201a2295;
  text-shadow: 0 1px 2px #ffe55ca8;
  outline: none;
}

.portfolio-meta-link:active {
  background: #fffdd6;
  color: #161428;
}
.meta-link-icon {
  margin: 0 4px;
  font-size: 1em;
  vertical-align: middle;
  filter: drop-shadow(0 0 2px #ffeaa7bb);
}

/* NPC dialog stays as before (for reference) */
.portfolio-npc-dialog {
  background: #222531ee;
  color: #ffeaa7;
  border-radius: 8px;
  box-shadow: 0 4px 19px rgba(0,0,0,0.11);
  padding: 16px 28px;
  font-family: var(--font-body, monospace);
  font-size: 1.08rem;
  border: 2px solid #e8cb62;
  pointer-events: auto;
  max-width: 320px;
  text-align: center;
  z-index: 2000;
}

