.glitch-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 999;
  mix-blend-mode: normal;
}

/* 📺 INTENSE CRT SCANLINES - Way more visible! */
.crt-scanlines {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 200%;
  background-image: 
    /* Primary scanlines - bright and obvious */
    linear-gradient(
      to bottom,
      transparent 0%,
      rgba(0, 255, 0, 0.15) 40%,
      rgba(255, 255, 255, 0.1) 50%,
      rgba(0, 255, 0, 0.15) 60%,
      transparent 100%
    ),
    /* Secondary scanlines for density */
    linear-gradient(
      to bottom,
      transparent 0%,
      rgba(255, 0, 255, 0.08) 25%,
      transparent 26%,
      transparent 74%,
      rgba(255, 0, 255, 0.08) 75%,
      transparent 100%
    );
  background-size: 100% 6px, 100% 2px;
  animation: 
    scanlineMove 0.08s linear infinite,
    scanlineFlicker 0.5s ease-in-out infinite alternate;
  opacity: 0.4;
}

@keyframes scanlineMove {
  0% { transform: translateY(-100%); }
  100% { transform: translateY(0%); }
}

@keyframes scanlineFlicker {
  0% { opacity: 0.3; filter: brightness(1); }
  100% { opacity: 0.5; filter: brightness(1.2); }
}

/* ⚡ FIXED SCREEN STATIC - No more horizontal movement! */
.screen-static {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    /* Primary static pattern */
    radial-gradient(circle at 20% 30%, rgba(255, 255, 255, 0.3) 1px, transparent 1px),
    radial-gradient(circle at 80% 70%, rgba(0, 255, 255, 0.2) 1px, transparent 1px),
    radial-gradient(circle at 40% 80%, rgba(255, 0, 255, 0.25) 1px, transparent 1px);
  background-size: 6px 6px, 4px 4px, 8px 8px;
  /* 🔧 FIXED: Only vertical movement and scaling - NO translateX! */
  animation: 
    staticNoiseVertical 0.15s steps(12) infinite,
    staticIntensity 2s ease-in-out infinite;
  opacity: 0.15;
}

/* 🎯 NEW: Static noise with ONLY vertical movement */
@keyframes staticNoiseVertical {
  0% { transform: translateY(0px) scale(1); }
  8% { transform: translateY(-2px) scale(1.02); }
  16% { transform: translateY(-3px) scale(0.98); }
  25% { transform: translateY(2px) scale(1.01); }
  33% { transform: translateY(3px) scale(0.99); }
  41% { transform: translateY(3px) scale(1.02); }
  50% { transform: translateY(-2px) scale(0.97); }
  58% { transform: translateY(-3px) scale(1.03); }
  66% { transform: translateY(2px) scale(0.98); }
  75% { transform: translateY(1px) scale(1.01); }
  83% { transform: translateY(-2px) scale(0.99); }
  91% { transform: translateY(2px) scale(1); }
  100% { transform: translateY(0px) scale(1); }
}

@keyframes staticIntensity {
  0%, 100% { opacity: 0.1; }
  50% { opacity: 0.2; }
}

/* 🌈 RGB SHIFT - Only color effects, no movement */
.rgb-shift {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(90deg, 
      rgba(255, 0, 0, 0.25) 0%, 
      transparent 3%, 
      transparent 97%, 
      rgba(0, 255, 255, 0.25) 100%
    ),
    linear-gradient(180deg,
      rgba(0, 255, 0, 0.15) 0%,
      transparent 5%,
      transparent 95%,
      rgba(255, 0, 255, 0.15) 100%
    );
  animation: rgbColorShift 1.5s ease-in-out infinite;
}

@keyframes rgbColorShift {
  0%, 100% { 
    opacity: 0.6; 
    filter: hue-rotate(0deg) saturate(1);
  }
  25% { 
    opacity: 0.9; 
    filter: hue-rotate(90deg) saturate(1.2);
  }
  50% { 
    opacity: 0.7; 
    filter: hue-rotate(180deg) saturate(0.8);
  }
  75% { 
    opacity: 0.8; 
    filter: hue-rotate(270deg) saturate(1.1);
  }
}

/* 💥 FIXED Digital corruption - Only vertical movement */
.digital-corruption {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: 
    linear-gradient(90deg, 
      transparent 0%, 
      rgba(255, 255, 255, 0.1) 10%, 
      transparent 11%,
      transparent 89%,
      rgba(0, 255, 255, 0.15) 90%,
      transparent 100%
    );
  /* 🔧 FIXED: Only vertical corruption blocks */
  animation: corruptionBlocksVertical 0.3s steps(6) infinite;
}

@keyframes corruptionBlocksVertical {
  0% { transform: translateY(0px); opacity: 0.8; }
  16% { transform: translateY(-5px); opacity: 0.6; }
  33% { transform: translateY(3px); opacity: 0.9; }
  50% { transform: translateY(-2px); opacity: 0.7; }
  66% { transform: translateY(4px); opacity: 0.8; }
  83% { transform: translateY(-1px); opacity: 0.5; }
  100% { transform: translateY(0px); opacity: 0.8; }
}

/* 💎 PERFECT SPARKLES - Keep exactly as they are! */
.floating-diamonds {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.diamond-cross {
  position: absolute;
  animation: diamondFloat 4s ease-in-out infinite;
}

.diamond-symbol {
  display: block;
  font-size: 16px;
  color: #FFE55C;
  text-shadow: 
    0 0 5px #FFE55C,
    0 0 10px #FFE55C,
    0 0 15px #FFD700;
  animation: diamondSparkle 1s ease-in-out infinite alternate;
}

.diamond-type-0 .diamond-symbol { 
  color: #FFE55C;
  text-shadow: 0 0 8px #FFE55C, 0 0 16px #FFD700;
}

.diamond-type-1 .diamond-symbol { 
  color: #FF6B9D;
  text-shadow: 0 0 8px #FF6B9D, 0 0 16px #FF1493;
}

.diamond-type-2 .diamond-symbol { 
  color: #4CAF50;
  text-shadow: 0 0 8px #4CAF50, 0 0 16px #00FF00;
}

@keyframes diamondFloat {
  0% {
    transform: translateY(100vh) rotate(0deg) scale(0);
    opacity: 0;
  }
  15% {
    opacity: 1;
    transform: translateY(85vh) rotate(45deg) scale(1);
  }
  85% {
    opacity: 1;
    transform: translateY(-10vh) rotate(315deg) scale(1);
  }
  100% {
    transform: translateY(-25vh) rotate(360deg) scale(0);
    opacity: 0;
  }
}

@keyframes diamondSparkle {
  0% { 
    transform: scale(1) rotate(0deg);
    filter: brightness(1);
  }
  100% { 
    transform: scale(1.2) rotate(180deg);
    filter: brightness(1.5);
  }
}

/* 📺 CRT VIGNETTE - Completely stable */
.crt-vignette {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: 
    /* Outer dark vignette */
    radial-gradient(
      ellipse at center,
      transparent 60%,
      rgba(0, 0, 0, 0.3) 80%,
      rgba(0, 0, 0, 0.6) 100%
    ),
    /* Inner green glow like old CRT */
    radial-gradient(
      ellipse at center,
      rgba(0, 255, 0, 0.05) 0%,
      transparent 40%
    );
  box-shadow: 
    inset 0 0 100px rgba(0, 0, 0, 0.7),
    inset 0 0 50px rgba(0, 255, 0, 0.1);
}

/* 💥 Screen flicker effect */
.screen-flicker {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.05);
  animation: screenFlicker 0.1s steps(2) infinite;
}

@keyframes screenFlicker {
  0%, 90% { opacity: 0; }
  5%, 85% { opacity: 1; }
}

/* Performance optimizations */
.glitch-overlay * {
  will-change: transform, opacity;
  backface-visibility: hidden;
}

/* 📱 Mobile intense effects */
@media (max-width: 768px) {
  .crt-scanlines {
    background-size: 100% 4px, 100% 2px;
    opacity: 0.35;
  }
  
  .screen-static {
    background-size: 4px 4px, 3px 3px, 5px 5px;
    opacity: 0.12;
  }
  
  .rgb-shift {
    opacity: 0.5;
  }
}
