* {
  cursor: none !important;
}

/* Pixel trail effect */
.cursor-trail {
  position: fixed;
  width: 4px;
  height: 4px;
  border-radius: 50%;
  pointer-events: none;
  z-index: 9997;
  transform: translate(-50%, -50%);
  animation: trailFade 0.4s ease-out forwards;
}

@keyframes trailFade {
  0% {
    opacity: 0.8;
    transform: translate(-50%, -50%) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.3);
  }
}

/* Main cursor dot */
.custom-cursor {
  position: fixed;
  width: 8px;
  height: 8px;
  pointer-events: none;
  z-index: 9999;
  transform: translate(-50%, -50%);
  transition: all 0.06s ease;
  mix-blend-mode: difference;
}

.cursor-inner {
  width: 100%;
  height: 100%;
  background: var(--color-accent);
  border-radius: 50%;
  box-shadow: 0 0 8px var(--color-accent);
  animation: cursorPulse 2s ease-in-out infinite;
}

/* Hover state - dot grows and glows */
.custom-cursor.hovering {
  transform: translate(-50%, -50%) scale(1.5);
}

.custom-cursor.hovering .cursor-inner {
  box-shadow: 
    0 0 6px var(--cursor-color), 
    0 0 12px rgba(var(--color-accent-rgb, 194, 168, 93), 0.4);
  animation: cursorHoverPulse 0.8s ease-in-out infinite;
}

/* Pulse animations */
@keyframes cursorPulse {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.1);
    opacity: 0.8;
  }
}

@keyframes cursorHoverPulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
}

/* Mobile fallback */
@media (max-width: 768px) {
  * {
    cursor: auto !important;
  }
  
  .custom-cursor,
  .cursor-trail {
    display: none;
  }
}
