.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: radial-gradient(circle at center, #1a1a2e 0%, #16213e 50%, #0f0f0f 100%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  overflow: hidden;
  font-family: 'Press Start 2P', 'Courier New', monospace;
}

/* CRT Screen Effect */
.loading-screen::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: 
    repeating-linear-gradient(
      0deg,
      rgba(0, 255, 0, 0.03) 0px,
      rgba(0, 255, 0, 0.03) 1px,
      transparent 1px,
      transparent 2px
    );
  pointer-events: none;
  animation: scanlines 2s linear infinite;
}

/* Scanlines animation */
@keyframes scanlines {
  0% { transform: translateY(0); }
  100% { transform: translateY(2px); }
}

/* Main loading container */
.loading-container {
  text-align: center;
  position: relative;
  z-index: 1;
  max-width: 500px;
  padding: 2rem;
}

/* Logo/Title */
.loading-title {
  font-size: 1.5rem;
  color: #00ff88;
  margin-bottom: 2rem;
  text-shadow: 
    0 0 10px #00ff88,
    0 0 20px #00ff88,
    0 0 30px #00ff88;
  animation: titleGlow 2s ease-in-out infinite alternate;
  letter-spacing: 2px;
}

@keyframes titleGlow {
  from {
    text-shadow: 
      0 0 10px #00ff88,
      0 0 20px #00ff88,
      0 0 30px #00ff88;
  }
  to {
    text-shadow: 
      0 0 5px #00ff88,
      0 0 10px #00ff88,
      0 0 15px #00ff88;
  }
}

/* Loading bar container */
.loading-progress-container {
  width: 100%;
  height: 24px;
  background: #1a1a2e;
  border: 2px solid #00ff88;
  border-radius: 4px;
  margin-bottom: 1.5rem;
  position: relative;
  overflow: hidden;
  box-shadow: 
    inset 0 0 10px rgba(0, 255, 136, 0.2),
    0 0 10px rgba(0, 255, 136, 0.3);
}

/* Progress bar fill */
.loading-progress-bar {
  height: 100%;
  background: linear-gradient(
    90deg, 
    #00ff88 0%, 
    #00cc70 50%, 
    #00ff88 100%
  );
  transition: width 0.3s ease-out;
  position: relative;
  box-shadow: 0 0 15px rgba(0, 255, 136, 0.6);
}

/* Progress bar animated stripe effect */
.loading-progress-bar::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: repeating-linear-gradient(
    45deg,
    transparent 0,
    transparent 10px,
    rgba(255, 255, 255, 0.1) 10px,
    rgba(255, 255, 255, 0.1) 20px
  );
  animation: progressStripes 1s linear infinite;
}

@keyframes progressStripes {
  0% { transform: translateX(-20px); }
  100% { transform: translateX(20px); }
}

/* Progress text */
.loading-progress-text {
  color: #00ff88;
  font-size: 0.75rem;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: normal;
  letter-spacing: 1px;
}

/* Status text */
.loading-status {
  color: #88ccff;
  font-size: 0.625rem;
  margin-top: 1rem;
  min-height: 20px;
  animation: statusBlink 1.5s ease-in-out infinite;
  letter-spacing: 0.5px;
}

@keyframes statusBlink {
  0%, 50% { opacity: 1; }
  51%, 100% { opacity: 0.7; }
}

/* Dots animation for loading text */
.loading-dots::after {
  content: '';
  animation: dots 1.5s steps(4, end) infinite;
}

@keyframes dots {
  0%, 20% { content: ''; }
  40% { content: '.'; }
  60% { content: '..'; }
  80%, 100% { content: '...'; }
}

/* Retro gaming elements */
.loading-decorations {
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 0;
}

/* Floating pixels/sparkles */
.loading-pixel {
  position: absolute;
  width: 3px;
  height: 3px;
  background: #00ff88;
  animation: floatPixel 4s ease-in-out infinite;
  box-shadow: 0 0 6px #00ff88;
}

.loading-pixel:nth-child(1) {
  top: 20%;
  left: 10%;
  animation-delay: 0s;
}

.loading-pixel:nth-child(2) {
  top: 60%;
  left: 90%;
  animation-delay: 1s;
}

.loading-pixel:nth-child(3) {
  top: 80%;
  left: 20%;
  animation-delay: 2s;
}

.loading-pixel:nth-child(4) {
  top: 30%;
  left: 80%;
  animation-delay: 3s;
}

@keyframes floatPixel {
  0%, 100% {
    transform: translateY(0) scale(1);
    opacity: 0.7;
  }
  50% {
    transform: translateY(-20px) scale(1.2);
    opacity: 1;
  }
}

/* Fade out animation when loading complete */
.loading-screen.fade-out {
  animation: fadeOut 0.8s ease-out forwards;
}

@keyframes fadeOut {
  0% {
    opacity: 1;
    transform: scale(1);
  }
  100% {
    opacity: 0;
    transform: scale(1.05);
  }
}

/* Loading complete state */
.loading-complete .loading-title {
  color: #ffffff;
  animation: completePulse 0.5s ease-in-out 3;
}

@keyframes completePulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* Responsive design */
@media (max-width: 768px) {
  .loading-title {
    font-size: 1.2rem;
  }
  
  .loading-container {
    max-width: 90%;
    padding: 1rem;
  }
  
  .loading-progress-container {
    height: 20px;
  }
}

@media (max-width: 480px) {
  .loading-title {
    font-size: 1rem;
  }
  
  .loading-progress-text {
    font-size: 0.7rem;
  }
  
  .loading-status {
    font-size: 0.6rem;
  }
}
