/* Global sparkle background container */
.sparkle-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;  /* Don't interfere with user interactions */
  z-index: 1;           /* Behind content but above body */
  overflow: hidden;
}

/* Individual sparkle dots */
.sparkle-dot {
  position: absolute;
  background: var(--color-accent);
  border-radius: 50%;
  opacity: 0;
  animation: sparkle 3s ease-in-out infinite;
}

/* Sparkle animation - fade in/out with gentle pulse */
@keyframes sparkle {
  0%, 100% {
    opacity: 0;
    transform: scale(0.8);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.2);
  }
}

/* Alternate animation for variety */
.sparkle-dot:nth-child(even) {
  animation: sparkleAlt 4s ease-in-out infinite;
}

@keyframes sparkleAlt {
  0%, 100% {
    opacity: 0;
    transform: scale(1);
  }
  25% {
    opacity: 0.3;
    transform: scale(0.9);
  }
  75% {
    opacity: 0.8;
    transform: scale(1.1);
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .sparkle-dot {
    animation-duration: 2.5s; /* Slightly faster on mobile */
  }
}
