.mobile-construction {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: #000000;
  color: var(--color-bg, #f5f3ef);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  font-family: var(--font-body, 'Dansk', sans-serif);
  z-index: 9999;
}

/* Sparkling Background */
.sparkle-container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 1;
  perspective: 800px;
}

.sparkle {
  position: absolute;
  width: 16px;
  height: 16px;
  /* 🌊 SMOOTH: Continuous organic spin lifecycle */
  animation: smoothContinuousSpinLifecycle 5s cubic-bezier(0.25, 0.1, 0.25, 1) infinite;
  opacity: 0;
  transform-style: preserve-3d;
}

/* 💎 Keep your beautiful diamond design */
.sparkle::before {
  content: '';
  position: absolute;
  width: 16px;
  height: 16px;
  
  background: 
    radial-gradient(ellipse at 35% 25%, rgba(255, 255, 255, 0.95) 0%, transparent 45%),
    linear-gradient(45deg, 
      rgba(255, 255, 255, 0.25) 0%,
      transparent 25%,
      rgba(255, 235, 92, 0.3) 45%,
      transparent 55%,
      rgba(255, 255, 255, 0.2) 75%,
      transparent 100%
    ),
    linear-gradient(135deg, 
      #FFF59D 0%, #FFE55C 15%, #FFD700 30%, #F4D03F 45%,
      #E6B800 60%, #B8860B 75%, #8B6914 90%, #654321 100%
    );
  
  clip-path: polygon(50% 0%, 75% 25%, 75% 75%, 50% 100%, 25% 75%, 25% 25%);
  
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  
  box-shadow: 
    inset 2px 2px 1px rgba(255, 255, 255, 0.7),
    inset -1px -1px 2px rgba(139, 105, 20, 0.5),
    0 0 6px rgba(255, 215, 0, 0.8),
    0 0 12px rgba(255, 215, 0, 0.6),
    0 0 20px rgba(255, 215, 0, 0.4),
    0 0 30px rgba(255, 215, 0, 0.25),
    0 3px 6px rgba(0, 0, 0, 0.4),
    0 6px 12px rgba(0, 0, 0, 0.2);
    
  /* 🌟 GENTLE: Smooth continuous twinkle */
  animation: smoothDiamondTwinkle 4s ease-in-out infinite;
  transform-origin: center center;
}

/* Gem variations with smooth individual timing */
.sparkle:nth-child(4n) {
  width: 14px;
  height: 14px;
  animation-duration: 5.3s; /* Slightly different timing */
}

.sparkle:nth-child(4n)::before {
  width: 14px;
  height: 14px;
  background: 
    radial-gradient(ellipse at 35% 25%, rgba(255, 255, 255, 0.9) 0%, transparent 45%),
    linear-gradient(45deg, 
      rgba(255, 255, 255, 0.25) 0%, transparent 25%,
      rgba(255, 182, 193, 0.3) 45%, transparent 55%,
      rgba(255, 255, 255, 0.2) 75%, transparent 100%
    ),
    linear-gradient(135deg, 
      #FFCDD2 0%, #FF8A95 15%, #FF6B9D 30%, #F48FB1 45%,
      #E91E63 60%, #C2185B 75%, #AD1457 90%, #880E4F 100%
    );
  box-shadow: 
    inset 2px 2px 1px rgba(255, 255, 255, 0.6),
    inset -1px -1px 2px rgba(136, 14, 79, 0.5),
    0 0 6px rgba(233, 30, 99, 0.7), 0 0 12px rgba(233, 30, 99, 0.5),
    0 0 20px rgba(233, 30, 99, 0.35), 0 3px 6px rgba(0, 0, 0, 0.4);
  animation-duration: 4.2s;
}

.sparkle:nth-child(5n) {
  width: 18px;
  height: 18px;
  animation-duration: 4.7s;
}

.sparkle:nth-child(5n)::before {
  width: 18px;
  height: 18px;
  background: 
    radial-gradient(ellipse at 35% 25%, rgba(255, 255, 255, 0.9) 0%, transparent 45%),
    linear-gradient(45deg, 
      rgba(255, 255, 255, 0.25) 0%, transparent 25%,
      rgba(200, 230, 201, 0.3) 45%, transparent 55%,
      rgba(255, 255, 255, 0.2) 75%, transparent 100%
    ),
    linear-gradient(135deg, 
      #C8E6C9 0%, #A5D6A7 15%, #81C784 30%, #66BB6A 45%,
      #4CAF50 60%, #388E3C 75%, #2E7D32 90%, #1B5E20 100%
    );
  box-shadow: 
    inset 2px 2px 1px rgba(255, 255, 255, 0.7),
    inset -1px -1px 2px rgba(27, 94, 32, 0.6),
    0 0 8px rgba(76, 175, 80, 0.8), 0 0 16px rgba(76, 175, 80, 0.6),
    0 0 26px rgba(76, 175, 80, 0.4), 0 3px 6px rgba(0, 0, 0, 0.45);
  animation-duration: 3.9s;
}

.sparkle:nth-child(7n) {
  width: 20px;
  height: 20px;
  animation-duration: 5.8s;
}

.sparkle:nth-child(7n)::before {
  width: 20px;
  height: 20px;
  background: 
    radial-gradient(ellipse at 35% 25%, rgba(255, 255, 255, 0.95) 0%, transparent 45%),
    linear-gradient(45deg, 
      rgba(255, 255, 255, 0.25) 0%, transparent 25%,
      rgba(187, 222, 251, 0.3) 45%, transparent 55%,
      rgba(255, 255, 255, 0.2) 75%, transparent 100%
    ),
    linear-gradient(135deg, 
      #BBDEFB 0%, #90CAF9 15%, #64B5F6 30%, #42A5F5 45%,
      #2196F3 60%, #1976D2 75%, #1565C0 90%, #0D47A1 100%
    );
  box-shadow: 
    inset 2px 2px 1px rgba(255, 255, 255, 0.8),
    inset -1px -1px 2px rgba(13, 71, 161, 0.6),
    0 0 10px rgba(33, 150, 243, 0.9), 0 0 20px rgba(33, 150, 243, 0.7),
    0 0 32px rgba(33, 150, 243, 0.5), 0 3px 8px rgba(0, 0, 0, 0.5);
  animation-duration: 3.6s;
}

.sparkle:nth-child(11n) {
  width: 22px;
  height: 22px;
  animation-duration: 6.2s;
}

.sparkle:nth-child(11n)::before {
  width: 22px;
  height: 22px;
  background: 
    radial-gradient(ellipse at 35% 25%, rgba(255, 255, 255, 1) 0%, transparent 40%),
    conic-gradient(from 0deg at center,
      #FFB3BA 0deg, #FFDFBA 60deg, #FFFFBA 120deg, #BAFFC9 180deg,
      #BAE1FF 240deg, #D4BAFF 300deg, #FFB3BA 360deg
    ),
    linear-gradient(45deg, 
      rgba(255, 255, 255, 0.3) 0%, transparent 20%,
      rgba(255, 215, 0, 0.2) 40%, transparent 60%,
      rgba(255, 255, 255, 0.25) 80%, transparent 100%
    );
  box-shadow: 
    inset 3px 3px 1px rgba(255, 255, 255, 0.9),
    inset -2px -2px 3px rgba(0, 0, 0, 0.2),
    0 0 12px rgba(255, 215, 0, 0.9), 0 0 24px rgba(255, 179, 186, 0.6),
    0 0 36px rgba(186, 255, 201, 0.5), 0 4px 10px rgba(0, 0, 0, 0.6);
  animation-duration: 3.2s;
}

/* 🌊 SMOOTH CONTINUOUS: Perfect Spin Lifecycle */
@keyframes smoothContinuousSpinLifecycle {
  /* 🌅 FADE IN PHASE (0-15%) - Gentle appearance with slow acceleration */
  0% {
    opacity: 0;
    transform: translateY(20px) rotateY(0deg) scale(0.6);
  }
  5% {
    opacity: 0.2;
    transform: translateY(15px) rotateY(20deg) scale(0.7);
  }
  10% {
    opacity: 0.5;
    transform: translateY(10px) rotateY(50deg) scale(0.85);
  }
  15% {
    opacity: 1;
    transform: translateY(5px) rotateY(90deg) scale(1);
  }
  
  /* ⚡ ACCELERATION PHASE (15-25%) - Speed building up smoothly */
  18% {
    opacity: 1;
    transform: translateY(3px) rotateY(140deg) scale(1.02);
  }
  21% {
    opacity: 1;
    transform: translateY(1px) rotateY(210deg) scale(1.03);
  }
  25% {
    opacity: 1;
    transform: translateY(0px) rotateY(300deg) scale(1.04);
  }
  
  /* 🌪️ STEADY SPIN PHASE (25-75%) - Consistent high-speed rotation */
  30% {
    opacity: 1;
    transform: translateY(-2px) rotateY(480deg) scale(1.04);
  }
  35% {
    opacity: 1;
    transform: translateY(-4px) rotateY(720deg) scale(1.04);
  }
  40% {
    opacity: 1;
    transform: translateY(-6px) rotateY(1080deg) scale(1.04);
  }
  45% {
    opacity: 1;
    transform: translateY(-8px) rotateY(1440deg) scale(1.04);
  }
  50% {
    opacity: 1;
    transform: translateY(-9px) rotateY(1800deg) scale(1.04);
  }
  55% {
    opacity: 1;
    transform: translateY(-10px) rotateY(2160deg) scale(1.04);
  }
  60% {
    opacity: 1;
    transform: translateY(-11px) rotateY(2520deg) scale(1.04);
  }
  65% {
    opacity: 1;
    transform: translateY(-12px) rotateY(2880deg) scale(1.04);
  }
  70% {
    opacity: 1;
    transform: translateY(-13px) rotateY(3240deg) scale(1.04);
  }
  75% {
    opacity: 1;
    transform: translateY(-14px) rotateY(3600deg) scale(1.04);
  }
  
  /* 🛑 DECELERATION PHASE (75-85%) - Gradual slowdown */
  78% {
    opacity: 1;
    transform: translateY(-16px) rotateY(3720deg) scale(1.02);
  }
  81% {
    opacity: 1;
    transform: translateY(-18px) rotateY(3820deg) scale(1);
  }
  85% {
    opacity: 1;
    transform: translateY(-20px) rotateY(3900deg) scale(0.98);
  }
  
  /* 🌅 FADE OUT PHASE (85-100%) - Gentle disappearance */
  90% {
    opacity: 0.8;
    transform: translateY(-23px) rotateY(3960deg) scale(0.92);
  }
  95% {
    opacity: 0.4;
    transform: translateY(-26px) rotateY(4000deg) scale(0.85);
  }
  100% {
    opacity: 0;
    transform: translateY(-30px) rotateY(4020deg) scale(0.7);
  }
}

/* 🌟 SMOOTH TWINKLE: Gentle brightness variation */
@keyframes smoothDiamondTwinkle {
  0%, 100% {
    filter: brightness(1) hue-rotate(0deg) saturate(1);
    transform: scale(1);
  }
  20% {
    filter: brightness(1.1) hue-rotate(1deg) saturate(1.05);
    transform: scale(1.01);
  }
  40% {
    filter: brightness(1.2) hue-rotate(-1deg) saturate(1.1);
    transform: scale(1.02);
  }
  60% {
    filter: brightness(1.15) hue-rotate(2deg) saturate(1.08);
    transform: scale(1.015);
  }
  80% {
    filter: brightness(1.05) hue-rotate(-1deg) saturate(1.03);
    transform: scale(1.005);
  }
}

/* Keep all existing content styles unchanged */
.construction-content {
  text-align: center;
  z-index: 10;
  position: relative;
  padding: 2rem;
  max-width: 90vw;
  animation: slideInUp 1s ease-out;
}

@keyframes slideInUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.avatar-container {
  position: relative;
  display: inline-block;
  margin-bottom: 2rem;
  animation: avatarEntrance 1.5s ease-out 0.3s both;
}

@keyframes avatarEntrance {
  0% {
    opacity: 0;
    transform: scale(0.5) rotate(-10deg);
  }
  70% {
    transform: scale(1.05) rotate(2deg);
  }
  100% {
    opacity: 1;
    transform: scale(1) rotate(0deg);
  }
}

.thumbs-avatar {
  width: 120px;
  height: 120px;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
  filter: drop-shadow(0 0 20px rgba(245, 243, 239, 0.3));
  transition: transform 0.3s ease;
}

.thumbs-avatar:hover {
  transform: scale(1.1) rotate(5deg);
}

.avatar-glow {
  position: absolute;
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  background: radial-gradient(circle, rgba(245, 243, 239, 0.2) 0%, transparent 70%);
  border-radius: 50%;
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes glow {
  0% {
    opacity: 0.5;
    transform: scale(0.95);
  }
  100% {
    opacity: 1;
    transform: scale(1.05);
  }
}

.construction-text {
  margin-bottom: 2.5rem;
}

.main-title {
  font-family: var(--font-heading, 'Thorilat', sans-serif);
  font-size: clamp(1.8rem, 5vw, 2.5rem);
  font-weight: 700;
  color: var(--color-bg, #f5f3ef);
  margin-bottom: 0.5rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  animation: titleGlow 3s ease-in-out infinite alternate;
}

@keyframes titleGlow {
  0% {
    text-shadow: 0 0 10px rgba(245, 243, 239, 0.5);
  }
  100% {
    text-shadow: 0 0 20px rgba(245, 243, 239, 0.8), 0 0 30px rgba(194, 168, 93, 0.3);
  }
}

.subtitle {
  margin-top: 1rem;
}

.accent-text {
  font-size: 1.1rem;
  color: var(--color-accent, #c2a85d);
  font-weight: 500;
  letter-spacing: 1px;
  opacity: 0.9;
  animation: fadeInOut 2s ease-in-out infinite alternate;
}

@keyframes fadeInOut {
  0% { opacity: 0.7; }
  100% { opacity: 1; }
}

.loading-dots {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 2rem;
}

.dot {
  width: 8px;
  height: 8px;
  background-color: var(--color-accent, #c2a85d);
  border-radius: 50%;
  animation: loadingDots 1.5s ease-in-out infinite;
}

.dot:nth-child(2) {
  animation-delay: 0.3s;
}

.dot:nth-child(3) {
  animation-delay: 0.6s;
}

@keyframes loadingDots {
  0%, 60%, 100% {
    transform: scale(1);
    opacity: 0.7;
  }
  30% {
    transform: scale(1.4);
    opacity: 1;
  }
}

.rotate-hint {
  opacity: 0.6;
  font-size: 0.9rem;
  color: var(--color-bg, #f5f3ef);
  animation: slideInUp 1s ease-out 2s both;
}

.rotate-hint p {
  margin: 0;
  letter-spacing: 0.5px;
}

.floating-particles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 2;
}

.particle {
  position: absolute;
  width: 4px;
  height: 4px;
  background: linear-gradient(45deg, var(--color-accent, #c2a85d), transparent);
  border-radius: 50%;
  animation: particleFloat 4s ease-in-out infinite;
}

@keyframes particleFloat {
  0% {
    transform: translateY(100vh) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  90% {
    opacity: 1;
  }
  100% {
    transform: translateY(-100px) rotate(360deg);
    opacity: 0;
  }
}

@media (max-height: 600px) {
  .construction-content {
    padding: 1rem;
  }
  
  .thumbs-avatar {
    width: 80px;
    height: 80px;
  }
  
  .main-title {
    font-size: clamp(1.4rem, 4vw, 2rem);
  }
  
  .rotate-hint {
    font-size: 0.8rem;
  }
}

@media (orientation: landscape) and (max-height: 500px) {
  .construction-content {
    padding: 1rem;
  }
  
  .avatar-container {
    margin-bottom: 1rem;
  }
  
  .loading-dots {
    margin-bottom: 1rem;
  }
}
