@media (max-width: 1440px) {
  .landing {
    padding: 3rem 1.5rem;
  }

  .hero-layout {
    max-width: 1100px;
    padding: 0 clamp(12px, 1.5vw, 20px);
  }

  .intro-block {
    left: clamp(200px, 32vw, 320px); /* Reduced left margin for 13" screens */
    width: clamp(280px, 45vw, 520px); /* Adjusted width for better fit */
  }

  .intro-block h1 {
    font-size: clamp(2rem, 6.5vw, 6.5rem); /* Scaled down for smaller screens */
  }

  .avatar-8bit-container {
    width: clamp(180px, 28vw, 300px); /* Reduced avatar size for 13" */
  }
}

@media (max-width: 768px) {
  .landing {
    padding: 2rem 1rem;
    justify-content: center;
  }

  .hero-layout {
    grid-template-columns: 1fr;        
    grid-template-rows: 45vh 1fr;      
    gap: 1rem;
    flex-direction: column;
    text-align: center;
  }

  .intro-block {
    position: relative;
    left: auto;
    top: auto;
    transform: none;
    width: 100%;
    text-align: center;
  }

  .tagline {
    flex-direction: column;
    font-size: 1rem;
    transform: none;
    max-width: 100%;
  }

  .typed-heading {
    font-size: clamp(2rem, 6vw, 3rem);
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.landing {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: var(--color-bg);
  color: var(--color-navy);
  padding: 4rem 2rem;
  position: relative;
  min-height: 50vh;      
  overflow: hidden;
  width: 100%;
  max-width: 100vw; /* Prevent exceeding viewport */
  padding-left: 100px;
}

.hero-layout {
  position: relative;
  width: 100%;
  max-width: 1200px;       
  margin-left: auto;
  margin-right: auto;
  min-height: 60vh;       
  padding: 0 clamp(16px, 2vw, 24px);
}

.avatar {
  flex-shrink: 0;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.avatar:hover {
  transform: translateY(-1px) scale(1.01);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.15);
}

.intro-block {
  position: absolute;
  left: clamp(240px, 40vw, 400px);
  top: 50%;
  transform: translateY(-50%);
  width: clamp(300px, 50vw, 600px);
  text-align: left;
  z-index: 5;
  max-width: calc(100vw - 280px); /* Ensure it doesn't exceed viewport minus avatar space */
}

.intro-block h1 {
  font-family: var(--font-heading);
  font-size: clamp(2.5rem, 8.5vw, 8.5rem);
  font-weight: 700;
  line-height: 0.85;
  margin: 0;
  transform: translateY(5px);
  color: var(--color-navy);
}

.intro-block h2 {
  font-family: var(--font-body);
  font-size: clamp(1.25rem, 3vw, 2rem);
  font-weight: 600;
  color: var(--color-accent);
  margin-bottom: 0.5rem;
  transform: translateX(3px) !important;
}

.tagline {
  display: flex;
  align-items: baseline;
  font-size: 1.1rem;
  font-weight: 400;
  color: var(--color-text);
  transform: translateX(1px);
}

.fade-in {
  animation: fadeInUp 0.8s ease forwards;
}

.tagline-prefix {
  margin-right: 0.34rem;
  font-weight: 500;
  font-size: 1.3rem;
  border-bottom: 2px solid var(--color-accent);
  padding-bottom: 2px;
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.6s ease;
}

.tagline-prefix.show {
  opacity: 1;
  transform: translateY(0);
}

.typed-heading {
  font-family: var(--font-heading);
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.typed-wrapper {
  display: inline-block;
  font-size: 1.3rem;
  vertical-align: baseline;
  white-space: nowrap; 
}

/* Glitch Effect */
.glitch-text {
  position: relative;
  display: inline-block;
  animation: glitch 1.2s infinite;
}

@keyframes glitch {
  0% {
    text-shadow: 2px 0 red, -2px 0 blue;
  }
  20% {
    text-shadow: -2px 0 red, 2px 0 blue;
  }
  40% {
    text-shadow: 2px 0 red, -2px 0 blue;
  }
  60% {
    text-shadow: -2px 0 red, 2px 0 blue;
  }
  80% {
    text-shadow: 2px 0 red, -2px 0 blue;
  }
  100% {
    text-shadow: none;
  }
}

.tagline-prefix,
.typed-wrapper {
  will-change: transform, opacity;
}
