@media (max-width: 1440px) {
  body {
    font-size: 0.9rem;
  }
  
  .app-wrapper {
    min-height: 100vh; /* Changed from height to min-height */
    overflow-x: hidden; /* Prevent horizontal scroll */
  }
}

@media (max-width: 768px) {
  body {
    font-size: 0.95rem;
  }
}

@font-face {
  font-family: 'Thorilat';
  src: url('./assets/fonts/Thorilat.otf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Dansk';
  src: url('./assets/fonts/Dansk.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

:root {
  --font-heading: 'Thorilat', sans-serif;
  --font-body: 'Dansk', sans-serif;
  --color-bg: #f5f3ef;
  --color-text: #2e2e2e;
  --color-accent: #c2a85d;
  --color-navy: #1a2a40;
  --color-gold: #C49A3D;
  --color-gold-hover: #B5892F;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Global CRT / hazy scanline overlay applied to the whole app */
#global-overlay {
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: 2147483000;
  background: repeating-linear-gradient(
    0deg,
    transparent 0px,
    rgba(107, 63, 42, 0.015) 1px,
    rgba(107, 63, 42, 0.015) 2px,
    transparent 5px
  );
  mix-blend-mode: normal;
  opacity: 0.9;
}

/* Optional: super subtle hover warp site-wide (can skip for minimal look) */
body:hover #global-overlay {
  animation: global-crt-warp 0.18s ease-in-out;
}

@keyframes global-crt-warp {
  0%, 100% { transform: translateX(0) scaleX(1); }
  50%      { transform: translateX(0.5px) scaleX(1.001); }
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  background-color: var(--color-bg);
  color: var(--color-text);
  overflow-x: hidden; /* Prevent horizontal scroll globally */
}

h1 {
  letter-spacing: 4px;
}

p {
  letter-spacing: 0.05em;
}

.tight-text {
  letter-spacing: -0.5px;
}

.app-wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh; /* Changed from height to min-height */
  overflow-x: hidden; /* Prevent horizontal overflow */
  width: 100%; /* Ensure full width utilization */
}

.main-content {
  flex: 1;
  display: flex;
  justify-content: center; 
  align-items: center;
  width: 100%;
  max-width: 100vw; /* Prevent content from exceeding viewport */
}
